@import "fonts";
@import "firstIntro";
@import "firstMenu";

//reset
*{ margin: 0; padding: 0; }

html, body { width: 100%; height: 100%; }

//ustawienie koloru tła dla pierwszego intra gry
body{ background-color: lightgray; }

//ustawienie wysokoci i szerokości dla poszczególnych części strony (gry)
header, footer{ width: 100%; height: 10vh; }
nav{ width: 100%; height: 15vh; }
#alerts{ width: 100%; height: 5vh; }
#mainPart{ width: 100%; height: 60vh; }

//style dla tytułu gry w nagłówku
header{
  text-align: center;

  span:first-child{
    font-size: 200%;
    font-style: italic;
    font-weight: bold;
    -webkit-text-fill-color: #800000;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: black;
  }

  span:last-child{
        font-size: 150%;
        font-style: italic;
        font-weight: bold;
        -webkit-text-fill-color: #808000;
        -webkit-text-stroke-width: 1px;
        -webkit-text-stroke-color: black;
  }
}

//style dla przycisków pierwszego menu
#heroBtns{
  display: flex;
  justify-content: space-around;
}
